import { login, ajax, formatDate } from '@/static/js/base.js';
import api from '@/static/js/api.js';
export default {
	data() {
		return {
			URL: '',
			IMG_URL: '',
			formNode: {
			  project_name: '',
			  industry: '',
			  nature: '',
			  subject: '',
			  amount_of_money: '',
			  address: '',
			  turnover: '',
			  assets: '',
			  financing_amount: '',
        total_financing_amount: '',
        purpose: '',
        intention: '',
        contacts: '',
        mobile: '',
        financing_mode: '',
        summary: '',
        file: ''
			},
			images: [], // 仅用于上传
			imagesShow: [] // 仅用于显示
		};
	},
	onLoad(options) {
		const that = this;
		that.options = options;
		that.URL = api.URL;
		that.IMG_URL = api.IMG_URL;
	},
	methods: {
		// 表单数据获取
		getSetData(e) {
			let formNode = this.formNode;
			let name = e.currentTarget.dataset.name;
			let value = e.detail.value;
			formNode[name] = value;
			this.formNode = formNode;
		},
		uploadImg() {
			const that = this;
			let count = 9;
			wx.chooseImage({
				count: count,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: res => {
					uni.showToast({
						title: '图片上传中',
						icon: 'loading'
					});
					Promise.all(
						res.tempFiles.map(item => {
							return new Promise((resolve, reject) => {
								if (item.size > 10000000) {
									// if (item.size > 500) {
									uni.showToast({
										title: '上传图片不能大于5兆',
										icon: 'none',
										duration: 2000
									});
								} else {
									uni.uploadFile({
										url: api.home_upload_img,
										filePath: item.path,
										name: 'image',
		                formData: {
		                  disk: 'financing/file'
		                },
										header: {
											// Authorization: this.$parent.globalData.token
										},
										success: res => {
											resolve({
												path: JSON.parse(res.data).data
											});
										}
									});
								}
							});
						})
					)
						.then(e => {
							uni.hideToast();
							let imageShow = that.imagesShow;
							let image = that.images;
							for (let i = 0; i < e.length; i++) {
								imageShow.push(`${that.IMG_URL}/${e[i].path}`);
								image.push(e[i].path);
							}
							that.imagesShow = imageShow;
							that.images = image;
						})
						.catch(err => console.log(err));
				}
			});
		},
	  submit() {
	    const that = this
	    let formNode = that.formNode
	    formNode.file = that.images.toString()
	    console.log(formNode)
	    // 非空校验
	    for (let key in formNode) {
	      if (!formNode[key]) {
	        uni.showToast({
	            title: '请完善提交信息',
	            duration: 2000
	        });
	        return false
	      }
	    }
	    if (formNode.mobile.length != 11) {
	      uni.showToast({
	          title: '请完输入完整的手机号',
	          duration: 2000
	      });
	      return false
	    }
	    ajax({
	    	url: `${that.URL}/api/addFinancing`,
	    	type: "POST",
	      data: formNode
	    }).then(res => {
	      console.log(res)
	    	if (res.status_code == "ok") {
	        uni.showToast({
	          title: '发布成功',
	          icon: 'success',
	          duration: 2000
	        });
	        setTimeout(() => {
	          uni.navigateBack()
	        }, 2000)
	    	} else {
	        uni.showToast({
	          title: res.message,
	          duration: 2000
	        });
	      }
	    })
	  }
	}
};
